/* eslint-disable no-alert */
/* eslint-disable no-console */
import React, { useState } from 'react';
import styled from 'styled-components';
import { FetchDelHobby, FetchAddHobby } from 'api/admin/FetchEditHobby';
import { useNavigate } from 'react-router-dom';

const Container = styled.div`
  margin-top: 30vh;
  padding: 10vh;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f9f9f9;
`;

const Button = styled.button`
  padding: 8px 16px;
  margin-right: 10px;
  background-color: #0175ff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #0056b3;
  }
`;
const BackButton = styled.button`
  padding: 8px 16px;

  border: none;
  border-radius: 5px;
  cursor: pointer;
`;
const EditHobby: React.FC = () => {
  const [selectedHobby, setSelectedHobby] = useState('');
  const navigate = useNavigate();
  const handleAdd = async () => {
    try {
      await FetchAddHobby(selectedHobby).then(() =>
        alert(' .'),
      );
    } catch (error) {
      console.error('', error);
    }
  };
  const handleDelete = async () => {
    try {
      const selectedHobbyId = parseInt(selectedHobby, 10);

      await FetchDelHobby(selectedHobbyId).then(() =>
        alert(' .'),
      );
    } catch (error) {
      console.error(' ', error);
    }
  };

  return (
    <Container>
      {' '}
      <BackButton
        onClick={() => {
          return navigate('/admin');
        }}
      >
        
      </BackButton>
      <div></div>
      <input
        type="text"
        value={selectedHobby}
        onChange={(e) => setSelectedHobby(e.target.value)}
      />
      <Button onClick={handleAdd}> </Button>
      <Button onClick={handleDelete}> </Button>
      {/* <Button onClick={handleUpdate}> </Button> */}
    </Container>
  );
};

export default EditHobby;
